import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Header from './header/Header';
import Home from './home/Home';
import "./ShopApp.css"

const NotFound = () => <h1>Not Found</h1>

const ShopApp = () => {
    return (
        <div>
            <Header />
            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>
                <Route>
                    <NotFound />
                </Route>
            </Switch>
        </div>
    )


}
export default ShopApp;